import { useEffect, useState } from "react";

//agregamos abort controler. por ejemplo: cuando entras a netflix a ver una peli, y mientras se carga te arrepentis y vas a otra. nosotros como frontend tenemos que manejar eso para alivianar el backend


export function useFetch(urlApi, ParamDato) {

    const [data, setData] = useState([]);
    const [loader, setLoader] = useState(true)
    const [error, setError] = useState(false);
    const [controller, setController] = useState(null);


    useEffect(() => {

        const abortController = new AbortController();
        setController(abortController)


        //Make a GET request
        fetch(urlApi, { signal: abortController.signal })

            .then(response => {
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                return response.json();
            })
            .then(data => {
                //setPosts(data.Posts);
                // setTimeout(() => {

                if (ParamDato == 'Mensajes') {
                    // console.log("console DE MENSAJES")
                    // console.log(data)
                    let abc = 1
                }


                setData(data[ParamDato])

                // }, 15000);


            })
            .then(a => {
                setLoader(false)
                //lo dejo aca pero esta sujeto a evaluaciones
            })

            .catch(err => {
                if (error.name == "AbortError") { //no se por que hace esto aca, me parece innecesario ya que esta en la funcion handle
                    console.error("Request cancelled");
                } else {
                    console.error('Error:', err);
                    setError(err)
                }


            })
            .finally(exito => {
                //no anda muy bien este finally, lo probe con un console.log y settimeout en el then 2, y no se ejecutaba al ultimo como deberia
                // setLoader(false)
            })


        //esto se ejecuta cuando el componente se haya desmontado
        return () => abortController.abort();
        //cuando se desmonta un componente? cuando ya no es visible en pantalla


    }, [])




    // Creamos un manejador de ese abort. Para el ejemplo de netflix (lineas mas arriba)
    const handleCancelRequest = () => {
        if (controller) {
            controller.abort();
            setError("Request cancelled - abort controller")
        }
    }







    return { data, loader, error, handleCancelRequest }

}



