import { useState } from "react";
import { createContext } from "react";

const CronosContext = createContext();

//const initialCronos = null;

const CronosProvider = ({ children }) => {

    const [segundos, setSegundos] = useState(0);

    const data = {
        segundos,
        setSegundos,
    }

    return <CronosContext.Provider value={data}>{children}</CronosContext.Provider>
}

export { CronosProvider };// exporta como objeto
export default CronosContext;
















