import React, { useState, useEffect, useContext } from 'react';
import CronosContext from './Context/CronosContext';

function Reloj() { //{ refeParent }

  //ahora desde context  , segundos, setSegundos
  const { segundos, setSegundos } = useContext(CronosContext);

  const [activo, setActivo] = useState(false);

  const [horasTime, setHorasTime] = useState(0);
  const [minutosTime, setMinutosTime] = useState(0);
  const [segundosTime, setSegundosTime] = useState(0);


  useEffect(() => {
    let intervalo = null;
    if (activo) {

      intervalo = setInterval(() => {
        setSegundos(segundos => segundos + 1);

        setHorasTime(Math.floor(segundos / 3600))
        let segundosB = segundos % 3600;
        //le sumamos +1 a segundosB porque en realidad es problema de renderizado, como que no termina de renderizar. Entonces se hace ese artilugio
        setMinutosTime(Math.floor(Number(segundosB + 1) / 60))
        setSegundosTime(Number(segundosB + 1) % 60)
      }, 1000);

      //codigo de gpt que yo saque, lo vi innecesario
      // } else if (!activo && segundos !== 0) {
      //   clearInterval(intervalo); //funcion nativa de js
      //   console.log("intervalo borrado")
    }
    return () => clearInterval(intervalo);
    /*La función que se pasa como argumento a useEffect puede devolver una función de limpieza. 
    Esta función de limpieza se ejecuta cuando el componente se desmonta o cuando alguno de los valores especificados en la 
    lista de dependencias cambia.*/

  }, [activo, segundos]);


  const handleClickBtnReset = () => {
    setSegundos(0)
    setSegundosTime(0)
    setMinutosTime(0)
    setHorasTime(0)
  }


  return (
    <div className="cron-reloj-divMain">
      <h1>Cronómetro</h1>
      <p>Arrastra hacia alguna sección</p>
      <p className="cron_fwBold"  >
        {horasTime > 0 && horasTime.toString().padStart(2, "0") + ":"}
        {minutosTime.toString().padStart(2, "0")}:{segundosTime.toString().padStart(2, "0")}
        {/* <span>- {segundos}  segundos- </span> */}
        {/*  ref={refeParent} 28/11/23 */}
      </p>

      <div className="cron-reloj-divContainerButtons" >
        <button className="cron-btn" onClick={() => setActivo(!activo)}>
          {activo ? 'Pausar' : 'Iniciar'}
        </button>

        <button className="cron-btn" onClick={handleClickBtnReset}>Reiniciar</button>

      </div>
    </div>
  );
}

export default Reloj;