import React, { useEffect, useState } from 'react';
import ChatMessage from './ChatMessage';
import './styleSpidder.css';
import ConversationMessage from './ConversartionMessage';
import env from './variables_entorno'
import { useFetch } from './useFetch';


const Messages = ({ currentUser, lstUsers }) => {

    const [isConversation, setIsConversation] = useState(false)

    const [wantedChats, setWantedChats] = useState('')

    const [anotherPerson, setAnotherPerson] = useState(null)

    const [showTooltipNewMsg, setShowTooltipNewMsg] = useState(false)  // 13/6/24


    const handleChangeInput = (e) => {
        setWantedChats(e.target.value);
        //console.log("se escribio " + e.target.value)
    }

    const [refreshMessages, setRefreshMessages] = useState(false) //es solo para recargar los mensajes


    const [simuladorMensajes, setSimuladorMensajes] = useState([])
    //let simuladorMensajes = []
    //     {
    //         sender: 'valee',
    //         receiver: 'emanuel',
    //         message: 'Hola reyy como estas?',
    //         time: '1705250012543' //milisegundos
    //     },
    //     {
    //         sender: 'nacho65',
    //         receiver: 'emanuel',
    //         message: '1 Que haces pa',
    //         time: '1705410012000'
    //     },
    //     {
    //         sender: 'nacho65',
    //         receiver: 'emanuel',
    //         message: '1b Todo tranqui?',
    //         time: '1705419012000'
    //     },
    //     {
    //         sender: 'emanuel',
    //         receiver: 'nacho65',
    //         message: '0 Hola',
    //         time: '1705210012000'
    //     },
    //     {
    //         sender: 'nacho65',
    //         receiver: 'emanuel',
    //         message: '3Todo tranquilo por lo menos hermano',
    //         time: '1705490018200'
    //     },
    //     {
    //         sender: 'emanuel',
    //         receiver: 'nacho65',
    //         message: '2Bien y vos?',
    //         time: '1705450015100'
    //     },
    //     {
    //         sender: 'nacho65',
    //         receiver: 'valee',
    //         message: 'Buen dia',
    //         time: '1705445004543'
    //     },
    //     {
    //         sender: 'valee',
    //         receiver: 'nacho65',
    //         message: 'Escuchaste la cancion del biza?',
    //         time: '1705425000043'
    //     }
    // ]


    let apiUrlGetMessages = env.url_api + "/spiddergetallmsg";



    const { data: dataApiMessages, loader: loaderMessages, error: errorApiMessages } = useFetch(apiUrlGetMessages, 'Mensajes')


    useEffect(() => {


        // //Make a GET request
        // fetch(apiUrlGetMessages)
        //     .then(response => {
        //         if (!response.ok) {
        //             throw new Error('Network response was not ok');
        //         }
        //         return response.json();
        //     })
        //     .then(data => {
        //         console.log(data);
        //         console.log(data.Mensajes[0])

        //         setSimuladorMensajes(data.Mensajes);

        //     })
        //     .catch(error => {
        //         console.error('Error:', error);
        //     });


        setSimuladorMensajes(dataApiMessages);

        // console.log("Se actualizo mensajes setSimuladorMensajes")
        // console.log(dataApiMessages)

    }, [dataApiMessages, loaderMessages])  //refreshMessages es para que haga refresh al enviar mensaje
    //[],  refreshMessages, simuladorMensajes, errorApiMessages




    useEffect(() => {
        //pongo aca porque es cuando se envia un mensaje, tiene que recargar. No pude reutilizar el useEffect de arriba con el useFetch

        //Make a GET request
        fetch(apiUrlGetMessages)
            .then(response => {
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                return response.json();
            })
            .then(data => {
                // console.log(data);
                setSimuladorMensajes(data.Mensajes);
            })
            .catch(error => {
                console.error('Error:', error);
            });

        setSimuladorMensajes(dataApiMessages);

    }, [refreshMessages])



    // const filteredUsers = lstUsers.filter((item) =>
    //     item.username.toLowerCase().includes(wantedChats.toLowerCase())
    // );

    // // Lógica para determinar si no hay usuarios coincidentes
    // const noUsers = filteredUsers.length === 0;



    return (

        loaderMessages == true ? //simuladorMensajes != [] & &simuladorMensajes.length == 0.. || simuladorMensajes == [] || simuladorMensajes == undefined || simuladorMensajes == null
            (
                <div style={{ padding: 'auto', position: 'relative', display: 'flex', justifyContent: 'center' }}>
                    <div className="sp-loaderMsg" style={{ marginTop: '12rem', marginRight: '16rem', marginBottom: '8rem', marginLeft: '0', marginRight: '0' }}>
                    </div>
                </div>
            ) : (
                <>

                    <h3 className="mb-3 mt-5r">Mensajes</h3>

                    {/* input para buscar */}
                    {!isConversation &&
                        <>
                            <div className="input-group ">

                                <input type="search" className="form-control" placeholder='Busque chat aqui'

                                    aria-label="Enter username" aria-describedby="button-addon2"
                                    onChange={(e) => handleChangeInput(e)} value={wantedChats} />
                            </div>


                            {/* Lista las conversaciones recientes */}
                            <div className="sp-msg-containerconvers" >
                                {


                                    simuladorMensajes
                                        .sort(function (a, b) {
                                            return b.time - a.time;
                                        }) //ordenamos por fecha


                                        .reduce((acumulador, mensajeItem) => {
                                            const existe = acumulador.find(itemFind =>
                                                (itemFind.sender === mensajeItem.sender && itemFind.receiver === mensajeItem.receiver) ||
                                                (itemFind.sender === mensajeItem.receiver && itemFind.receiver === mensajeItem.sender)
                                            );
                                            if (!existe) {
                                                acumulador.push(mensajeItem);
                                            }
                                            return acumulador;
                                        }, [])



                                        //filtros para el array
                                        //hago este lio para que no me filtre por mi currentuser
                                        .filter(itemMensaje =>
                                            (itemMensaje.sender !== currentUser && itemMensaje.sender.includes(wantedChats)) ||
                                            (itemMensaje.receiver !== currentUser && itemMensaje.receiver.includes(wantedChats))
                                        )




                                        .map((itemMensaje, index) => (
                                            itemMensaje.sender == currentUser || itemMensaje.receiver == currentUser ? (
                                                <ChatMessage currentUser={currentUser} lstUsers={lstUsers}
                                                    itemMensaje={itemMensaje} isConversation={isConversation}
                                                    setIsConversation={setIsConversation}
                                                    setAnotherPerson={setAnotherPerson}
                                                    key={index}
                                                />
                                            ) : (null)
                                        ))


                                }

                            </div>




                            {/* Mensaje nuevo */}
                            <div onMouseEnter={() => setShowTooltipNewMsg(true)}
                                onMouseLeave={() => setShowTooltipNewMsg(false)}
                                className="sp-msg-containerNewMsg" >
                                <i className="bi bi-plus-circle sp-msg-iconNewMsg"></i>
                            </div>

                            <div className={`sp-msg-tooltipNewMsg ${showTooltipNewMsg ? 'sp_show' : 'sp_hide'}`}>
                                Enviar nuevo mensaje
                            </div>


                        </>
                    }

                    {
                        // si se seleccionó alguna conversacion
                        isConversation &&

                        <ConversationMessage
                            isConversation={isConversation} setIsConversation={setIsConversation} currentUser={currentUser}
                            anotherPerson={anotherPerson}
                            setRefreshMessages={setRefreshMessages}
                            refreshMessages={refreshMessages}

                            simuladorMensajesFiltrado={
                                simuladorMensajes
                                    .sort(function (a, b) {
                                        return a.time - b.time;
                                    }) //ordenamos por fecha

                                    .filter((item) => item.sender == currentUser || item.receiver == currentUser)
                            }  //filtrar solo por los que hizo click ej (chat de vale con nacho65)

                        />

                    }


                    {/* Lista de usuarios */}
                    {/* {wantedUsers != '' &&
                <div>
                    {noUsers ? (
                        <p>No se encontraron usuarios</p>
                    ) : (
                        <ul className="sp-sp-ulUsers">

                            {filteredUsers.map((item) => (
                                <li>
                                    <a  onClick={() => { setIrAPerfil(item)}}>
                                        {item.username}
                                    </a>
                                </li>
                            )
                            )}
                        </ul>
                    )
                    }
                </div>
            }


              Mostrar perfil (luego de hacer click)  
            {
                irAPerfil &&
                <>
                    <hr className='m-b-neg2r' />
                    <MyProfile currentUserObj={irAPerfil} lstAllPosts={lstAllPosts}
                        modificarArrayPostsInherited={modificarArrayPostsInherited} />
                </>
            } */}



                </>

            )

    );
};

export default Messages;