import React, { useState, useEffect, useRef } from 'react';


const combinacionesColores = [
  {
    colorTarjeta: '#000000', // negro
    colorTexto: '#ffffff'// blanco
  },
  {
    colorTarjeta: '#3498db',//(Azul)
    colorTexto: '#ffffff'//(Blanco)
  },
  {
    colorTarjeta: '#2ecc71',//(Verde esmeralda)
    colorTexto: '#ffffff' //(Blanco)
  },
  {
    colorTarjeta: '#F3BBE6',// blanco violeta
    colorTexto: '#000000'// negro
  },
  {
    colorTarjeta: ' #e74c3c',//(Rojo)
    colorTexto: '#ffffff' //(Blanco)
  },
  {
    colorTarjeta: '#f39c12',//(Naranja)
    colorTexto: '#ffffff'// (Blanco)
  }
]




function Seccion({ objItemSeccion, onDropP, onDragOverP, limpiarSegundosSeccion,
  onDragLeaveP, deleteSeccionP, setColoresTarjetaTexto, setNombreSeccion, setTimeManually }) {

  // name={itemSeccion.nameSeccion}

  const { id: idP, sumaSegundos, colorTarjeta, colorTexto, nameSeccion } = objItemSeccion;

  const [horasTime, setHorasTime] = useState(0);
  const [minutosTime, setMinutosTime] = useState(0);
  const [segundosTime, setSegundosTime] = useState(0);

  const [coloresNum, setColoresNum] = useState(0); //empieza con el indice 0


  const refModal = useRef(null)

  const refModal_hours = useRef(null)
  const refModal_minutes = useRef(null)
  const refModal_seconds = useRef(null)




  useEffect(() => {

    //alert("SUMASEGUNDOS CAMBIADO " + nameSeccion)
    // if (nameSeccion == 'TRES') { //para test
    //   //alert(sumaSegundos)
    //   console.log("OBJETO DE TRES:")
    //   console.log(objItemSeccion)
    // }

    if (sumaSegundos > 0) {
      //debugger
      //alert("seteamos sumaTIMES CON: " + sumaSegundos)

      //console.log("%c sumasegundos useEffect", "background: darkgreen")

      setTimeout(() => {
        //jaja segunda vez en el dia que me salva las papas este artilugio.
        //Antes: teniendo 3 secciones editadas manualmente con btn editar: uno(1h1m1s), dos(2h2m2s), 3(3h3m3s) -> al borrar la del medio (dos), queda: uno(1h1m1s), 3(2h3m3s)
        //Solucion: este timeout
        setHorasTime(Math.floor(sumaSegundos / 3600))
      }, 0);


      let sumaSegundosB = sumaSegundos % 3600;
      setMinutosTime(Math.floor(Number(sumaSegundosB) / 60))


      setTimeout(() => {
        //Problema: teniendo 3 secciones: uno(1s), dos(2s), 3(3s) -> al borrar la del medio (dos), queda: uno(1s), 3(2s)
        //Solucion: este set time out. 26/12/23
        //buenooooo funciona lpm, era problema de asincronia. le pedi a ia que me lo haga sincrono, y con este settimeout (...no se porque...) se soluciono
        setSegundosTime(Number(sumaSegundosB) % 60);
      }, 0);


      //console.log(horasTime + minutosTime + segundosTime)
      //BUENO ENCONTRE EL PROBLEMA LPM 1.37AM 8/12/23
      //HACE TODO BIEN LOS CALCULOS PERO ES PROBLEMA DE LA PUTA EJECUCION DE JAVASCRIPT, NUNCA TERMINA DE EJECUTAR setSegundosTime EN EL 66. POR LO QUE SE CLAVA EN EL 2 VIEJO Y NUNCA SE RECALCULA

    } else { //si sumaSegundos es menor a 0, que iguale todo a 0
      setHorasTime(0)
      setMinutosTime(0)
      setSegundosTime(0)
    }

  }, [sumaSegundos]);


  //console.log("renderizado seccion")

  useEffect(() => {
    setSegundosTime(segundosTime) //para que rerenderice al pedo
    //console.log("renderizar por cambio de colorTarjeta")
    setHorasTime(horasTime)
  }, [colorTarjeta]);



  const handleKeyDown = (event) => { //para que no ingresen 'enter'
    if (event.keyCode === 13) {
      event.preventDefault();
    }
  };



  const handleChangeNameSeccion = (e) => {
    setNombreSeccion(idP, e.target.value)
  }


  //cambiar color de tarjeta
  // const [colorTarjeta, setColorTarjeta] = useState(combinacionesColores[coloresNum].colorTarjeta);
  // const [colorTexto, setColorTexto] = useState(combinacionesColores[coloresNum].colorTexto);

  useEffect(() => {
    setColoresTarjetaTexto(
      idP,
      {
        colorTarjeta: combinacionesColores[coloresNum].colorTarjeta,
        colorTexto: combinacionesColores[coloresNum].colorTexto
      }
    );
  }, [coloresNum]) //cuando cambie la variable coloresNum


  const handleColor = e => {
    //e.target.value para el input de color
    if (coloresNum == 5) { //es el maximo de combinacionesColores
      setColoresNum(0)
    } else {
      setColoresNum(coloresNum + 1)
    }
  }



  /*modal ventana flotante editar reloj*/
  function abrirModal() {
    //document.getElementById('modal').style.display = 'block';
    refModal.current.style.display = 'block'
  }

  function cerrarModal() {
    refModal.current.style.display = 'none';
  }

  function guardarEdicion() {
    // Obtener valores editados del reloj
    let horas = parseInt(refModal_hours.current.value) || 0;
    let minutos = parseInt(refModal_minutes.current.value) || 0;
    let segundos = parseInt(refModal_seconds.current.value) || 0;

    // Aquí puedes agregar lógica para guardar la edición del reloj
    // Por ejemplo, puedes actualizar un reloj en la interfaz gráfica con los nuevos valores
    setTimeManually({
      id: idP,
      hours: horas,
      minutes: minutos,
      seconds: segundos
    })

    cerrarModal(); // Cierra la modal después de guardar
  }






  return (
    <div
      className="cron-secc-divSeccion"
      onDrop={onDropP}
      onDragLeave={onDragLeaveP}
      onDragOver={(event) => {
        event.preventDefault();
        event.stopPropagation(); // Evita que se propague el evento a elementos secundarios
        onDragOverP(event);
      }}
      style={{ backgroundColor: colorTarjeta, color: colorTexto }}
      id={idP} >

      <button
        className="cron-secc-btnDelete"
        onClick={() => deleteSeccionP(idP)}
        id-tarjeta={idP} //id de la tarjeta madre
      >X</button>


      {/* <input type="color" onChange={handleColor} /> */}

      <button onClick={handleColor}
        className="cron-secc-btnColor"
        id-tarjeta={idP}>
        <i id="iconoPaletaColores" id-tarjeta={idP}
          className="bi bi-palette cron-iconoColor cron_fwBold"></i>
      </button>


      {/* <h1 style={{ pointerEvents: 'none' }}   >SECCION</h1> */}

      {/* <div id="editable" contenteditable="true" placeholder="Escriba aquí..."></div>   */}


      <input className="cron-secc-inputNombreSeccion"
        //value={nombreSeccion}
        maxLength="50"
        //onChange={(event) => setNombreSeccion(event.target.value)}
        onKeyDown={handleKeyDown} placeholder="Seccion... " autoComplete="off"
        //onClick={handleNameClick} 5/12/23
        value={nameSeccion} onChange={handleChangeNameSeccion}
        id-tarjeta={idP} style={{ color: colorTexto }} />

      <p className="cron-secc-pSumaSegundos" >SUMASEGUNDOS: {sumaSegundos}</p>

      <div id="container-time" id-tarjeta={idP} className="cron-secc-containerTime">

        <p style={{ pointerEvents: 'none' }}>
          {/* Real HORA:   */}
          {horasTime > 0 && horasTime.toString().padStart(2, "0") + ":"}
          {minutosTime.toString().padStart(2, "0")}:{segundosTime.toString().padStart(2, "0")}
        </p>

        <i id="iconoEdit" id-tarjeta={idP} className="bi bi-pencil-fill cron-secc-btnEdit-icon" onClick={abrirModal} >
        </i>

      </div>


      {/* <button className="cron-secc-btnEdit" id-tarjeta={idP} > </button> */}


      {/* ventana flotante modal edicion hora manual */}
      {/* <button id="btnEditar" onClick={abrirModal}>Editar Reloj</button> */}

      <div id="modal" className="cron-secc-modal" ref={refModal}>
        <div className="modal-content cron-secc-modal-content"> {/**modal-content es una clase propia de bootstrap parece, que si la saco me desconfigura el moda, por eso agregue la mia propia al lado */}

          <span className="cron-secc-close" onClick={cerrarModal}>&times;</span>

          <label htmlFor="horaInput" className="cron_clBlack">Horas:</label>
          <input type="number" ref={refModal_hours} id="horaInput" min="0" max="23" />

          <label htmlFor="minutoInput" className="cron_clBlack">Minutos:</label>
          <input type="number" ref={refModal_minutes} id="minutoInput" min="0" max="59" />

          <label htmlFor="segundoInput" className="cron_clBlack">Segundos:</label>
          <input type="number" ref={refModal_seconds} id="segundoInput" min="0" max="59" />
          <button onClick={guardarEdicion}>Guardar</button>
        </div>
      </div>

      {/* En este enfoque, hemos añadido pointerEvents: 'none' a los estilos de los elementos secundarios (h1 y p). Esto hará que estos elementos no respondan a eventos, incluido el evento de arrastre. Como resultado, el evento de arrastre se dirigirá únicamente al div contenedor y no se propagará a los elementos secundarios. Esto debería asegurar que el div reciba el evento de arrastre sin que los elementos internos interfieran. */}

      <button id-tarjeta={idP} className="cron-secc-btnCero" onClick={() => limpiarSegundosSeccion(idP)}>00:00</button>

    </div>
  );
}

export default Seccion;

