import React, { useContext, useState } from 'react';

import Reloj from './Reloj';
import Seccion from './Seccion';
import './cronosStyles.css'
import CronosContext from './Context/CronosContext';
import MainSpidder from '../spidder/MainSpidder';




let initialSecciones = {
    id: Date.now(), //funciona solo para el primero
    nameSeccion: "",
    sumaSegundos: 0,
    colorTarjeta: '#000000',
    colorTexto: '#ffffff'
}



const CronosIndex = () => {

    //para el hijo:
    //const [segundos, setSegundos] = useState(0); //pasado a context

    const { segundos, setSegundos } = useContext(CronosContext);
    // console.log("Estoy pensando que esto de arriba esta innecesariamente y que deberia ser   local")


    /* deprecadas 23/11/23
        const [dragging, setDragging] = useState(null);
        const [sumaSegundos, setSumaSegundos] = useState(0);
        const [divReceptorActual, setDivReceptorActual] = useState(false);
        const [tiempoSecciones, setTiempoSecciones] = useState(initialStateTiempoSecciones)
        const refeDivAgrandado = useRef();
    */

    // const segundosRef = useRef(null);5/12/23
    // const [divAgrandadoActual, setDivAgrandadoActual] = useState("");

    const [secciones, setSecciones] = useState([{ ...initialSecciones }]); //hago este lio porque si no hace referencia en lugar de tomarlo de molde, por lo tanto al crear una tarjeta nueva nunca empezaba desde 0

    const setColoresTarjetaTexto = (id, objColores) => {
        setSecciones(secciones.map(item => {
            if (item.id == id) {
                return {
                    ...item,
                    colorTarjeta: objColores.colorTarjeta,
                    colorTexto: objColores.colorTexto
                }
            }
            return item
        }))
    }


    // const handleDragStart = (e) => {
    //      //setDragging(e.target.cloneNode(true)); //esto era para copiar el elemento dentro de otro elemento
    //  };


    /*const handleDragEnter = (e) => {
        // console.log("divReceptorActual vs e")
        // console.log(divReceptorActual)
        // console.log( e.target)
        e.preventDefault();
        alert("handleDragEnter")
        // Haz algo cuando el elemento es arrastrado dentro de la zona
        // if (dragging) {
        //     e.target.appendChild(dragging);
        //     setDragging(null);
        // }
    };

    const handleDragOver = (e) => {
        e.preventDefault();// Necesario para permitir el evento onDrop
        alert("handleDragOver")
    }*///5/12/23

    // const handleDragEnd = (e) => {

    //     if (divReceptorActual != true) { // si es otro div  (es para evitar el acumulado innecesario cuando lo pasas varias veces por encima al mouse)
    //         // console.log(e.target)
    //         // alert("bueno")
    //         // setTiempoSecciones(
    //         //     {
    //         //         ...tiempoSecciones,
    //         //         [e.target.id]: "asd"
    //         //     }
    //         // )
    //         console.log(tiempoSecciones)
    //         setDivReceptorActual(true);
    //         let timeArrastrado = Number(segundosRef.current.textContent)
    //         // console.log(timeArrastrado)
    //         let newTime = sumaSegundos + timeArrastrado;
    //         setSumaSegundos(newTime)
    //     }
    //     setDivReceptorActual(false)//para ver si se reinicia
    // }


    // const allowDrop = (event) => {
    //     event.preventDefault();
    // };
    // const [isDragging, setIsDragging] = useState(false);

    // const handleDragStart = () => {
    //     setIsDragging(true);
    // };

    // const handleDragEnd = () => {
    //     setIsDragging(false);
    // };

    // const handleDrop = (event) => {
    //     event.preventDefault();
    //     if (isDragging) {
    //         // Agregar lógica para manejar la acción en divB después de la soltada.
    //         console.log("DivA fue soltado en Sección");
    //         setIsDragging(false);
    //     }
    // };


    /*
        const handleDrag = (event) => {
            console.log("BRASIL")
            //event.dataTransfer.setData("text", event.target.id); 30/11/23
        }*///5/12/23



    const onDragLeave = (event) => {// El cursor del mouse o el elemento arrastrado ha salido del área
        //console.log("onDragLeave - achicar tarjeta")
        quitarAgrandadoTarjeta(event);
    }


    const handleAllowDrop = (event) => {
        event.preventDefault();

        //console.log("handleAllowDrop - agranda tarjeta")

        if (event.target.type == 'text' || event.target.type == 'submit' ||
            event.target.id == "iconoPaletaColores" || event.target.id == 'container-time' ||
            event.target.id == "iconoEdit") {
            //quiere decir que esta queriendo dropear sobre el input, que en realidad esta mal

            let tarjetaMadre = document.getElementById(event.target.getAttribute("id-tarjeta"))
            //busca el id de la tarjeta madre

            tarjetaMadre.classList.add('cron-index-resaltarCard')
            tarjetaMadre.classList.remove('cron-index-resaltarCard_quit')
        } else {
            event.target.classList.add('cron-index-resaltarCard')
            event.target.classList.remove('cron-index-resaltarCard_quit')
        }
    }






    const quitarAgrandadoTarjeta = (eventoParametro) => {
        //eventoParametro es event de las funciones disparadoras
        if (eventoParametro.target.type == 'text' || eventoParametro.target.type == 'submit' ||
            eventoParametro.target.id == "iconoPaletaColores" || eventoParametro.target.id == 'container-time' ||
            eventoParametro.target.id == "iconoEdit") {

            let tarjetaMadre = document.getElementById(eventoParametro.target.getAttribute("id-tarjeta"))

            tarjetaMadre.classList.remove('cron-index-resaltarCard')
            tarjetaMadre.classList.add('cron-index-resaltarCard_quit')

        } else {
            eventoParametro.target.classList.remove('cron-index-resaltarCard')
            eventoParametro.target.classList.add('cron-index-resaltarCard_quit')
        }
    }




    const handleDrop = (event) => {
        event.preventDefault();
        //console.log("handleDrop")

        //eliminamos el agrandado de div
        quitarAgrandadoTarjeta(event)

        let idTarjeta = event.target.id;//por default el id del event.target

        if (event.target.type == 'text' || event.target.type == 'submit' ||
            event.target.id == "iconoPaletaColores" || event.target.id == 'container-time' ||
            event.target.id == "iconoEdit") {
            //si es boton o input o algun elemento extra, que busque en el atributo id-tarjeta (manualmente tengo que poner en id-tarjeta el id de la tarjeta madre)
            idTarjeta = event.target.getAttribute("id-tarjeta");
        }

        //const data = event.dataTransfer.getData("text"); 28/11/23

        let seccionActual = secciones.filter((itemSeccion) => itemSeccion.id == idTarjeta)[0];

        let tiempoAnterior = seccionActual.sumaSegundos || 0;

        setSecciones(
            secciones.map((itemSeccion) => {
                if (idTarjeta == itemSeccion.id) {
                    itemSeccion.sumaSegundos = tiempoAnterior + segundos
                }
                return itemSeccion
            })
        )
    }




    const limpiarSegundosSeccion = (id) => {
        setSecciones(
            secciones.map((itemSec) => {
                if (itemSec.id == id) {
                    itemSec.sumaSegundos = 0;
                    //console.log("Se puso e 0")
                }
                return itemSec
            })
        )
    }



    const handleAddSeccion = () => {
        setSecciones(prevSecciones => {
            return [...prevSecciones, { ...initialSecciones, id: Date.now() }]
        })
    }


    const deleteSeccion = (id) => {
        // alert("BORRADO")
        //console.log("seccion previa:")
        //console.log(secciones)
        setSecciones(
            secciones.filter((itemSec) => itemSec.id != id)
        )
        //console.log("nueva seccion")
        //console.log(secciones)
    }


    const setNombreSeccion = (id, newTextoNombre) => {
        setSecciones(secciones.map(item => {
            if (item.id == id) {
                return {
                    ...item,
                    nameSeccion: newTextoNombre
                }
            }
            return item
        }))
    }



    const setTimeManually = (objData) => {
        const { id, hours, minutes, seconds } = objData;
        //console.log(objData)
        // alert("setTimeManually")
        setSecciones(secciones.map(item => {
            if (item.id == id) {
                return {
                    ...item,
                    //seteamos manualmente el nuevo tiempo (que se ingresó desde el input)
                    sumaSegundos: (hours * 3600) + (minutes * +60) + seconds
                }
            }
            return item
        }))
    }






    //cuando cambie secciones
    // useEffect(() => {

    //     console.log('%c Cambio algo en las secciones ', 'background: #222; color: #bada55');
    //     console.log(Date.now())
    //     console.log(secciones)

    // }, [secciones])










    return (
        <>
            <div
                // className="draggable" 28/11/23
                className="cron-index-divArrastrable"
                draggable="true"
            // onDragStart={handleDrag}  30/11/23
            >
                <Reloj
                //refeParent={"segundosRef"} 28/11/23
                // draggable="true"
                // usado desde context segundos={segundos} setSegundos={setSegundos} 
                />
            </div>


            <button className="cron-index-btnNuevaSeccion cron-btn" onClick={handleAddSeccion}>
                Nueva Sección
            </button>


            <div className='cron-index-containerSeccion'>
                {
                    secciones.length > 0 ? (
                        secciones.map((itemSeccion) => (
                            <Seccion
                                key={itemSeccion.id}
                                objItemSeccion={itemSeccion}
                                setColoresTarjetaTexto={setColoresTarjetaTexto}
                                setNombreSeccion={setNombreSeccion}

                                setTimeManually={setTimeManually}

                                //les puse useCallback a las de abajo pero no funciono como esperaba y se lo saque:
                                onDropP={handleDrop}
                                onDragOverP={handleAllowDrop}
                                onDragLeaveP={onDragLeave}
                                limpiarSegundosSeccion={limpiarSegundosSeccion}
                                deleteSeccionP={deleteSeccion}
                            />
                        ))

                    ) : (

                        <div className='cron-index-divNoSeccion'>
                            No hay secciones, tiene que agregar una
                        </div>
                    )

                }
            </div>
        </>
    );
};

export default CronosIndex;












